<template>
  <div id="clients-table">
    <p>
      <button type="button" class="btn btn-default" @click="add">添加</button>
    </p>
    <p>当前在线：{{online}}</p>
    <table class="table table-striped table-bordered table-hover table-condensed">
      <thead>
        <tr>
          <td>key</td>
          <td>token</td>
          <td>备注</td>
          <td>创建时间</td>
          <td>状态</td>
          <td>更新文件</td>
          <td>操作</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="client in clients" :key="client._id">
          <td>{{client.key}}</td>
          <td>{{client.token}}</td>
          <td>{{client.description}}</td>
          <td>{{client.createTime}}</td>
          <td>{{client.status}}</td>
          <td>{{client.updateFile ? client.updateFile.name : '无' }}</td>
          <td>
            <button type="button" class="btn btn-default" @click="edit(client)">编辑</button>
            <button type="button" class="btn btn-default" @click="setting(client)">更新</button>
            <button type="button" class="btn btn-default" :value="client._id" @click="remove">删除</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'clients',
  methods: {
    ...mapActions(['loadClients', 'showModal', 'removeClient']),
    add() {
      this.showModal({
        show: true,
        type: 'add',
        title: '添加客户端',
        form: 'client',
        data: {}
      });
    },
    edit({ _id: id, key, token, description }) {
      this.showModal({
        show: true,
        type: 'edit',
        title: '编辑客户端',
        form: 'client',
        data: { id, key, token, description }
      });
    },
    setting({ _id: clientId, updateFile }) {
      const data = { clientId };
      if (updateFile) {
        const { _id: fileId } = updateFile;
        Object.assign(data, { fileId });
      }
      this.showModal({
        show: true,
        type: 'setting',
        title: '设置客户端更新文件',
        form: 'setting',
        data
      });
    },
    remove(event) {
      if (confirm('确定要删除客户端？')) {
        this.removeClient(event.target.value);
      }
    }
  },
  computed: mapGetters(['clients', 'online']),
  created() {
    this.loadClients();
  }
};
</script>



