export default {
  state: { list: [], dists: [] },
  mutations: {
    loadClients(state, clients) {
      state.list = clients;
    },
    loadDists(state, dists) {
      state.dists = dists;
    }
  },
  actions: {
    loadClients({ commit }) {
      // 加载客户端列表
      fetch('/api/clients', {
        credentials: 'same-origin'
      }).then(response => response.json())
        .then((result) => {
          if (result.code === 1) {
            commit('loadClients', result.data);
          }
        });

      // 加载更新包列表
      fetch('/api/files?type=dists', {
        credentials: 'same-origin'
      }).then(response => response.json())
        .then((result) => {
          if (result.code === 1) {
            commit('loadDists', result.data);
          }
        });
    },
    // 删除客户端
    removeClient({ dispatch }, id) {
      fetch(`/api/client/${id}`, {
        method: 'DELETE',
        credentials: 'same-origin'
      }).then(response => response.json())
        .then((result) => {
          alert(result.msg);
          dispatch('loadClients');
        }).catch((err) => {
          console.log(err);
        });
    }
  }
};



