<template>
  <form class="form-horizontal" style="padding: 15px;">
    <input type="hidden" v-model="clientId"/>
    <div class="form-group">
      <label class="col-sm-4 control-label">选择更新包</label>
      <div class="col-sm-5">
        <select v-model="selected">
          <option value="">未设置</option>
          <option v-for="option in dists" :value="option.id">
            {{ option.name }}
          </option>
        </select>
      </div>
    </div>
  </form>
</template>

<script>
import { mapState, mapGetters } from 'vuex';

export default {
  name: 'settingForm',
  computed: {
    ...mapGetters(['dists']),
    ...mapState({
      data: state => state.modal.data,
    }),
    clientId() {
      return this.data.clientId;
    },
    selected: {
      get() {
        return this.data.fileId || '';
      },
      set(value) {
        this.$store.commit('updateFormData', Object.assign({}, this.data, { fileId: value }));
      }
    }
  }
};
</script>



