import 'babel-polyfill';

import Vue from 'vue';
import { sync } from 'vuex-router-sync';

import store from './store';
import router from './router';
import App from './App';

import files from './store/modules/files';
import clients from './store/modules/clients';


// 通过在一个动态注册的模块中管理应用的路由状态
sync(store, router);

function enter(path) {
  switch (path) {
    case '/files':
      store.registerModule('files', files); break;
    case '/clients':
      store.registerModule('clients', clients); break;
    default: return;
  }
}

function leave(path) {
  switch (path) {
    case '/files':
      store.unregisterModule('files'); break;
    case '/clients':
      store.unregisterModule('clients'); break;
    default: return;
  }
}

// 根据路由状态注册动态模块
router.beforeEach((to, from, next) => {
  enter(to.path);
  leave(from.path);
  next();
});

new Vue({
  el: '#app',
  store,
  router,
  render: h => h(App)
});



