import 'isomorphic-fetch';

import Vue from 'vue';
import Vuex from 'vuex';

import modal from './modules/modal';

// 安装插件
Vue.use(Vuex);  // 状态管理

export default new Vuex.Store({
  modules: { modal },
  getters: {
    files: state => state.files.list,
    clients: state => state.clients.list.map(client => ({
      ...client,
      status: client.status === 'offline' ? '离线' : '在线'
    })),
    online: state => state.clients.list.reduce((count, client) =>
      count + (client.status === 'online' ? 1 : 0)
    , 0),
    dists: state => state.clients.dists.map(file => ({
      id: file._id,
      name: file.name,
    })),
  }
});



