<template>
  <div ref="modal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="modal-title">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
          <h4 class="modal-title">{{title}}</h4>
        </div>
        <div class="modal-body">
          <component v-bind:is="form"></component>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" @click="commit">确认</button>
          <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable no-undef */
import { mapState, mapActions } from 'vuex';
import FileForm from './forms/FileForm';
import ClientForm from './forms/ClientForm';
import SettingForm from './forms/SettingForm';

export default {
  name: 'modal',
  components: {
    file: FileForm,
    client: ClientForm,
    setting: SettingForm,
  },
  methods: {
    ...mapActions(['closeModal', 'submitForm']),
    fileForm() {
      const formData = new FormData();
      const { fileType, uploadFile } = this.data;
      formData.append('fileType', fileType);
      formData.append('uploadFile', uploadFile);
      return formData;
    },
    commit() {
      switch (this.form) {
        case 'client':
        case 'setting':
          this.submitForm({
            url: `/api/client/${this.type}`,
            modal: this.$refs.modal,
            headers: {
              'Content-Type': 'application/json'
            },
            formData: JSON.stringify(this.data),
            action: 'loadClients'
          });
          break;
        case 'file':
          this.submitForm({
            url: `/api/file/${this.type}`,
            modal: this.$refs.modal,
            formData: this.fileForm(),
            action: 'loadFiles'
          });
          break;
        default: return;
      }
    }
  },
  computed: mapState({
    show: state => state.modal.show,
    type: state => state.modal.type,
    title: state => state.modal.title,
    form: state => state.modal.form,
    data: state => state.modal.data,
  }),
  watch: {
    show(isShow) {
      if (isShow) $(this.$refs.modal).modal('show');
    }
  },
  mounted() {
    const that = this;
    $(this.$refs.modal).on('hidden.bs.modal', () => {
      that.closeModal();
    });
  }
};
</script>



