const initState = {
  show: false,
  type: '',
  title: '',
  form: '',
  data: {},
};

export default {
  state: { ...initState },
  /* eslint-disable no-unused-vars */
  mutations: {
    showModal(state, form) {
      Object.assign(state, { ...form });
    },
    closeModal(state) {
      Object.assign(state, { ...initState });
    },
    updateFormData(state, formData) {
      state.data = { ...formData };
    }
  },
  actions: {
    // 显示模态框
    showModal({ commit }, form) {
      commit('showModal', form);
    },
    // 关闭模态框
    closeModal({ commit }) {
      commit('closeModal');
    },
    // 修改表单控件的值
    // updateFormData({ commit }, formData) {
    //   commit('updateFormData', formData);
    // },
    // 提交表单
    submitForm({ dispatch }, { url, headers, formData, action, modal }) {
      fetch(url, {
        headers,
        method: 'POST',
        credentials: 'same-origin',
        body: formData
      }).then(response => response.json())
        .then((result) => {
          alert(result.msg);
          $(modal).modal('hide');
          return dispatch(action);
        }).catch((err) => {
          console.log(err);
        });
    },
  }
};



