<template>
  <form class="form-horizontal" style="padding: 15px;">
    <div class="form-group">
      <label class="col-sm-4 control-label">文件类型</label>
      <div class="col-sm-5">
        <select v-model="fileType">
          <option value="dists">更新包</option>
          <option value="imgs">图片</option>
          <option value="logs">日志</option>
          <option value="others">其他</option>
        </select>
      </div>
    </div>
    <div class="form-group">
      <label class="col-sm-4 control-label">文件</label>
      <div class="col-sm-5">
        <input type="file" class="form-control" @change="upload"/>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  name: 'fileForm',
  data() {
    return {
      fileType: 'dists',
      uploadFile: {},
    };
  },
  methods: {
    upload(event) {
      this.uploadFile = event.target.files[0];
    },
    updateFormData() {
      this.$store.commit('updateFormData', this.$data);
    }
  },
  watch: {
    fileType: 'updateFormData',
    uploadFile: 'updateFormData',
  }
};
</script>



