export default {
  state: { list: [] },
  mutations: {
    loadFiles(state, files) {
      state.list = files;
    }
  },
  actions: {
    // 加载文件列表
    loadFiles({ commit }, query) {
      fetch(`/api/files${query || ''}`, {
        credentials: 'same-origin',
      }).then(response => response.json())
        .then((result) => {
          if (result.code === 1) {
            commit('loadFiles', result.data);
          }
        });
    },
    // 删除文件
    removeFile({ dispatch }, id) {
      fetch(`/api/file/${id}`, {
        method: 'DELETE',
        credentials: 'same-origin'
      }).then(response => response.json())
        .then((result) => {
          alert(result.msg);
          dispatch('loadFiles');
        }).catch((err) => {
          console.log(err);
        });
    }
  }
};



