<template>
  <form class="form-horizontal" style="padding: 15px;">
    <input type="hidden" v-if="type === 'edit'" v-model="client.id"/>
    <div class="form-group">
      <label class="col-sm-4 control-label">key</label>
      <div class="col-sm-5">
        <input type="text" class="form-control" v-model="client.key"/>
      </div>
    </div>
    <div class="form-group">
      <label class="col-sm-4 control-label">token</label>
      <div class="col-sm-5">
        <input type="text" class="form-control" v-model="client.token"/>
      </div>
    </div>
    <div class="form-group">
      <label class="col-sm-4 control-label">备注</label>
      <div class="col-sm-5">
        <textarea class="form-control" v-model="client.description"></textarea>
      </div>
    </div>
  </form>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'clientForm',
  computed: {
    ...mapState({
      type: state => state.modal.type,
      data: state => state.modal.data,
    }),
    client: {
      get() {
        return this.data;
      },
      set(value) {
        this.$store.commit('updateFormData', value);
      }
    }
  }
};
</script>



